@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;600&display=swap');
* {color:#000000; font-family: 'Rubik';}

body {
  background-color: #FFFCE0;
}

.code {
  font-family: Consolas;
}
.anim {
  font-weight: 600;
}
