#blog {
  width: fit-content;
}*/

img {
  max-width: 100%;
}

.title {
  font-size: large;
  margin-top: 20px;
  margin-bottom: 20px;
}

.small {
  font-size: smaller;
}

.row {
  display: flex;
  flex-direction: row;
}

.profileMenu .row {
  justify-content: space-between;
  align-items: center;
}

.profile {
  position: fixed;
  z-index: 0;
  width: 20%;
  max-width: 80px;
  aspect-ratio: 1 / 1;
  background-color:#542309;
  bottom: 0px;
  margin: 5px;
  right: 0;
  border-radius: 40px;
  overflow: hidden;
  background-image: url(./images/user1.png);
  background-size: 100%;
}

.profileMenu {
  transition: ease-in-out;
  padding: 20px;
  position: fixed;
  width: auto;
  left: 5%;
  right: 5%;
  bottom: 20px;
  height: 50%;
  border-radius: 20px;
  background-color:burlywood;
  display: flex;
  flex-direction: column;
}

.profileMenu * {
  margin: 5px;
}

input {
  border-color: #000000;
  border-width: 1px;
}

.submit {
  height: 8%;
  min-height: 20px;
  max-height: 30px;
  border-radius: 20px;
  box-shadow: none;
  text-align: left;
}

.input {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background-color: #FFFCE0;
  border-radius: 10px;
}

.input input {
  height: 60%;
  border-radius: 10px;
}

.questOverlay {
  z-index: 1;
  position: fixed;
  width: 80%;
  top: 5%;
  left: 10%;
  bottom: 5%;
  right: 10%;
  height: 90%;
  margin: 0px;
  padding: 0px;
  background-color:#FFFCE0;
  outline-color: #000000;
  outline-offset: 0px;
  outline-width: 10px;
  border-radius: 30px;
  overflow-x: hidden;
  overflow-y: scroll;
}

.header {
  position: sticky;
  display: flex;
  flex-direction: column;
  top: 0px;

}

.close {
  display: flex;
  justify-content: flex-end;
  flex-direction: row;
  background-color: #542309
}

#exit {
  position: relative;
  float: right;
  margin: 8px;
  width: 10%;
  max-width: 50px;
  aspect-ratio: 1 / 1;
  height: auto;
  background-size: 100%;
  background-repeat: no-repeat;
}

.sender {
  background-color: #ffffff;
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
  align-items: center;
}

.questAvatar {
  margin: -10px 5px -10px 5px;
  float: left;
  width: 20%;
  max-width: 80px;
  background-size: 100%;
  aspect-ratio: 1 / 1;
  background-repeat: no-repeat;
}

.questBody {
  overflow: hidden;
  padding: 20px;
}
.questBody p {
  overflow: hidden;
  text-align: justify;
}

.footer {
  width: auto;
  display: flex;
  position: sticky;
  justify-content: center;
  align-items: center;
  top: 100%;
  background-color: #542309;
  height: auto;
}
.footer p {
  color: #ffffff;
  font-style: italic;
  font-size: 20px;
  margin: 15px;
}

.imgContainer {
  display: flex;
  justify-content: center;
  align-items: center;
}

.page {
  display: grid;
  grid-template-columns: auto auto auto;
  flex-direction: row;
  margin: 5px;
  padding: 5px;
  align-items: center;
}

.Day {
  width: auto;
  height: 200px;
  background-position: 0;
  background-size: 100%;
  border-radius: 20px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 5px;
  padding: 10px;
  transition: ease 1s;
}

@media (max-width: 800px) {
  .page {
    flex-direction: column;
    grid-template-columns: auto;
  }
}

.titleDiv, .unavailable {
  height: auto;
  width: auto;
  background-color: #FFFCE0;
  font-weight: 400;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.unavailable {
  padding: 10px;
  border-radius: 30px;
  margin: 10px 10px 0px 10px;
  background-color: #161925;
}

.unavailable p {
  color: antiquewhite;
}

.titleDiv {
  margin: 0px;
  border-radius: 0px 0px 20px 20px;
  border-width: 2px;
  border-color: #000000;
}

.titleDiv {
  position: sticky;
  top: -1px;
}

.titleDiv p {
  padding-top: 10px;
  font-size: 30px;
  margin: 1px;
  text-align: center;
}

.titleDiv img {
  margin: -8px;
}

.unavailable {
  font-size: 30px;
}

.hide {
  border-color: transparent;
  border: none;
  background-color: transparent;
}